import { VueShowdown } from 'vue-showdown';
import showdownHighlight from 'showdown-highlight';
import { mapState, mapActions, mapMutations } from 'vuex';
import dayjs from 'dayjs';
import Gitalk from 'gitalk';
// import Comment from './Comment';
import Tag from '@/components/common/Tag';

export default {
  head() {
    return {
      title: `${this.article.title || '文章详情'}`,
      titleTemplate: '%s - DJL箫氏的博客!',
      htmlAttrs: {
        lang: 'en',
        amp: true
      },
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: '这是DJL箫氏的博客的文章详情页面'
        }
      ]
    };
  },
  components: {
    VueShowdown,
    // Comment,
    Tag
  },
  data() {
    return {
      showdownHighlight,
      options: {
        omitExtraWLInCodeBlocks: true,
        ghCodeBlocks: true
      },
      openTags: false,
      selectTags: [],
      articleTags: []
    };
  },
  computed: {
    ...mapState({
      article: (state) => state.article.currentArticle,
      tags: (state) => state.tag.allTags
    }),
    author() {
      return this.article.createUser;
    }
  },
  asyncData({ store, route }) {
    return store.dispatch('article/getArticle', route.params.id);
  },
  mounted() {
    const gitalk = new Gitalk({
      title: this.article.content,
      clientID: '0defc6b7dde11263449a',
      clientSecret: 'c91f6f8a6b463251f6185b85b22c9bc37d8b65ab',
      repo: 'blog', // The repository of store comments,
      owner: 'djlxiaoshi',
      admin: ['djlxiaoshi'],
      id: location.pathname, // Ensure uniqueness and length less than 50
      distractionFreeMode: false // Facebook-like distraction free mode
      // For more available options, check out the documentation below
    });

    gitalk.render('comments');

    this.articleTags = Array.isArray(this.article.tags)
      ? this.article.tags.map((tag) => tag._id)
      : [];
  },
  methods: {
    ...mapActions({
      getArticle: 'article/getArticle',
      getAllTags: 'tag/getAllTags'
    }),
    ...mapMutations({
      setCurrentArticle: 'article/setCurrentArticle'
    }),
    formatTime(time) {
      return time ? dayjs(time).format('YYYY-MM-DD') : '';
    },
    isChecked(tag) {
      const flag = !!this.article.tags.find(
        (articleTag) => articleTag._id === tag._id
      );
      return flag;
    },
    handleTagsParams() {
      this.saveArticleTags(this.articleTags);
    },
    goToTagDetails(tag) {
      if (tag) {
        this.$router.push(`/tag/${tag._id}`);
      }
    }
  }
};
